  padding: 2rem; }

.button {
  height: 40px;
  background: none;
  padding: 0 20px;
  overflow: visible;
  border-radius: 5px;
  margin-left: 1rem;
  letter-spacing: 1px;
  font-size: 13px;
  font-size: 0.8125rem;
  color: #B6B6B6;
  text-transform: uppercase;
  border: 2px solid #CBCBCB;
  font-weight: 600;
  -webkit-transition: all 0.12s linear;
  -moz-transition: all 0.12s linear;
  transition: all 0.12s linear; }
  .button:first-child {
    margin-left: 0; }

.button-action {
  border-color: #2BBB13;
  color: #30A81C; }
  .button-action:hover {
    background: #2BBB13;
    color: #FFFFFF; }

.button-general {
  border-color: #2E94C4;
  color: #2A8CBA; }
  .button-general:hover {
    background: #2E94C4;
    color: #FFFFFF; }

.button-warning {
  border-color: #EC6868;
  color: #DB6363; }
  .button-warning:hover {
    background: #EC6868;
    color: #FFFFFF; }

.button.disabled {
  cursor: not-allowed; }

.divider {
  border: 0;
  height: 1px;
  background: #f1f1f1; }
  .divider.wide {
    margin: 2rem 0; }

.dropzone-area {
  width: 100%;
  height: 200px;
  position: relative;
  border: 2px dashed #CBCBCB;
  -webkit-transition: all 0.12s linear;
  -moz-transition: all 0.12s linear;
  transition: all 0.12s linear; }
  .dropzone-area:hover {
    border: 2px dashed #2E94C4; }

.dropzone-area input {
  position: absolute;
  cursor: pointer;
  top: 0px;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0; }

.dropzone-text {
  position: absolute;
  top: 50%;
  text-align: center;
  transform: translate(0, -50%);
  width: 100%; }
  .dropzone-text span {
    display: block; }

.dropzone-title {
  font-size: 13px;
  font-size: 0.8125rem;
  color: #787878;
  letter-spacing: 0.4px; }

.dropzone-info {
  font-size: 13px;
  font-size: 0.8125rem;
  color: #A8A8A8;
  letter-spacing: 0.4px; }

.dropzone-button {
  position: absolute;
  top: 10px;
  right: 10px;
  display: none; }

.dropzone-preview {
  position: relative; }
  .dropzone-preview:hover .dropzone-button {
    display: block; }

.dropdown {
  position: relative;
  background: none;
  border: none;
  padding: 0;
  font-size: 13px;
  font-size: 0.8125rem;
  color: #787878;
  letter-spacing: 0.4px; }
  .dropdown.open .dropdown {
    color: #0C0C0C; }

.open > .dropdown-menu {
  display: block; }

.dropdown-menu {
  position: absolute;
  left: 0;
  z-index: 1000;
  display: none;
  margin: 5px 0 0;
  list-style: none;
  border-radius: 5px;
